@import "../../../protons/global";

.jcc-switchboard {
  @include section-wrapper;
  text-align: center;
  @include u-padding(3);

  .jcc-header-group {
    @include u-padding-bottom(4);
  }
}

.jcc-switchboard--has-background-color {
  @include u-bg('primary-lighter');
}

.jcc-switchboard__container {
  @include section-container;
}

.jcc-switchboard__tiles {
  @include add-list-reset;
  @include at-media("desktop") {
    @include grid-row;
    @include grid-gap;
  }

  .jcc-tile {
    @include u-border(1px, "primary");
    @include u-bg('primary-lighter');
  }
}

.jcc-switchboard__tiles--secondary {

  .jcc-tile {
    @include u-border(1px, "secondary");
    @include u-bg('secondary-lighter');
  }
}

.jcc-switchboard__tile {
  margin-bottom: 1rem;

  @include at-media(desktop) {
    @include grid-col(4);
  }
}
